* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
}

.carousel {
  background: #fafafa;
  margin-bottom: 40px;
  width: 500px;
}

.carousel-cell {
  width: 100%;
  height: 600px;
  margin-right: 10px;
  background: #8c8;
  counter-increment: carousel-cell;
}

/* cell number */
.carousel-cell:before {
  display: block;
  text-align: center;
  content: counter(carousel-cell);
  line-height: 200px;
  font-size: 80px;
  color: white;
}

.carousel-nav .carousel-cell {
  height: 80px;
  width: 100px;
}

.carousel-nav .carousel-cell:before {
  font-size: 50px;
  line-height: 80px;
}

.carousel-nav .carousel-cell.is-nav-selected {
  background: #ed2;
}
